export default {
    name: 'App',
    data () {
        return {
            catFact: null,
            catFacts: ["Cats make about 100 different sounds. Dogs make only about 10.","The technical term for a cat’s hairball is a bezoar.","There are cats who have survived falls from over 32 stories (320 meters) onto concrete.","Owning a cat can reduce the risk of stroke and heart attack by a third.","Wikipedia has a recording of a cat meowing, because why not?","Adult cats only meow to communicate with humans.","Cats can't taste sweetness.","rerewr","People who own cats have on average 2.1 pets per household, where dog owners have about 1.6.","The Persian is the most popular pedigreed cat breed in North America.","Cats are often lactose intolerant, so stop givin' them milk!","Basically all cartoon cats lied to us: Raw fish is off the table for cats as well.","The oldest cat video on YouTube dates back to 1894.","In the 1960s, the CIA tried to turn a cat into a bonafide spy by implanting a microphone into her ear and a radio transmitter at the base of her skull. She somehow survived the surgery but got hit by a taxi on her first mission.","The technical term for \"hairball\" is \"bezoar.\"","Female cats are typically right-pawed while male cats are typically left-pawed.","Cats make more than 100 different sounds whereas dogs make around 10.","Cats and humans have nearly identical sections of the brain that control emotion.","A cat has five toes on his front paws, and four on the back, unless he's a polydactyl.","There are 45 Hemingway cats living at the author's former home in Key West, Fla.","Abraham Lincoln kept four cats in the White House.","Cats use their whiskers to detect if they can fit through a space.","Cats love to eat olives or for that matter anything preserved in brine.","Evidence suggests domesticated cats have been around since 3600 B.C., 2,000 years before Egypt's pharaohs.","Every year, nearly four million cats are eaten in China as a delicacy.","I don't know anything about cats.","A group of cats is called a clowder.","Cats sleep 70% of their lives.","A cat can jump up to six times its length.","A house cat is faster than Usain Bolt.","Cats only sweat through their foot pads.","Kangaroos can't hop backwards","Original kitty litter was made out of sand but it was replaced by more absorbent clay in 1948.","The CIA spent US$20 million in the 60s training cats to spy on the Soviets. The first spy cat was hit by a taxi.","Here is a video of some cats in zero gravity. youtu.be/O9XtK6R1QAk","Since cats treat us like cats and depend on us for things like food, water, and opening the door to let them out, they do recognize, that we are in some way in charge — the “big cat” in the shared territory. As territorial animals, our cats are constantly wondering why we’re not doing other cat things that the big cat would normally do in their territory. In fact, the “let me in, let me out, let me in” phenomenon is a good example. The bigger cat ostensibly rules the territory and therefore should be the one patrolling and marking it with pee so other cats stay away. But since humans don’t do this, indoor-outdoor cats reluctantly take on the role themselves. The apparent neuroticism of cats wanting to go outside every five minutes only to be let right back in is funny to us because it seems so silly and unnecessary. But to the cats, it’s very necessary (and frustrating) to cover for their dumb pals.","Cats can distinguish different flavors in water.","In most US states, declawing cats is legal but in the European Union it is not.","Cats can hear the ultrasonic noises that rodents and dolphins make to communicate.","By the time a cat is 9 years old, it will only have been awake for three years of its life.","A female cat is called a “molly” or a “queen”.","Domestic cats spend about 70 percent of the day sleeping. And 15 percent of the day grooming.","Cats have fur","Cats are the most popular pet in the United States: There are 88 million pet cats and 74 million dogs.","In tigers and tabbies, the middle of the tongue is covered in backward-pointing spines, used for breaking off and gripping meat.","The world's richest cat is worth $13 million after his human passed away and left her fortune to him.","The frequency of a domestic cat's purr is the same at which muscles and bones repair themselves.","When cats grimace, they are usually \"taste-scenting.\" They have an extra organ that, with some breathing control, allows the cats to taste-sense the air.","A cat's purr may be a form of self-healing, as it can be a sign of nervousness as well as contentment.","Your cat recognizes your voice but just acts too cool to care (probably because they are).","A cat's cerebral cortex (the part of the brain in charge of cognitive information processing) has 300 million neurons, compared with a dog's 160 million.","In the 15th century, Pope Innocent VIII began ordering the killing of cats, pronouncing them demonic.","Cats have a longer-term memory than dogs, especially when they learn by actually doing rather than simply seeing.","Polydactyl cats are also referred to as \"Hemingway cats\" because the author was so fond of them.","When asked if her husband had any hobbies, Mary Todd Lincoln is said to have replied \"cats.\"","Cats can change their meow to manipulate a human. They often imitate a human baby when they need food, for example.","One legend claims that cats were created when a lion on Noah's Ark sneezed and two kittens came out.","It was illegal to slay cats in ancient Egypt, in large part because they provided the great service of controlling the rat population.","In Korea and Japan, there is a Cat Cafe where you can go to drink coffee and hang out with cats for hours.","When cats leave their poop uncovered, it is a sign of aggression to let you know they don't fear you.","Owning a cat can reduce the risk of heart attacks and strokes by more than a third, researchers found.","Cats \"knead\" because of seperation from their mothers","Your cat's instincts tell her that a paperweight or knickknack could turn out to be a mouse. Her poking paw would send it scurrying, giving her a good game. This is probably why cats always seem to be knocking the glasses off your counter tops!","The fear of cats is called \"Ailurophobia\"","Black cats are less likely to be adopted because of their \"appearance\".","Due to the controversy, though loved by most, the Kashmir is overlooked by many cat fanciers.","Cats can survive falls from up to 65 feet or more.","The goddess of love, beauty, and fertility in Norse mythology, Freyja was the first cat lady. She is depicted in stories as riding a chariot that was drawn by cats.","Cat owners are 25% likely to pick George Harrison as their favorite Beatle.","Cat owners are 17% more likely to have a graduate degree than non cat owners.","Only 11.5% of people consider themselves \"cat people\".","\"Cat people\" are 11% more likely to be introverted than others.","The Egyptian Mau is the oldest breed of cat, and is the fastest pedigreed cat.","Most cats don't like water because their coats do not insulate them well enough.","Cats were mythic symbols of divinity in ancient Egypt.","The place where Julius Caesar was murdered is now a cat sanctuary.","The Bombay cat breed was developed to resemble a miniature panther.","Cats use their whiskers to judge whether they’ll fit through a space.","Kittens sleep so much because the growth hormone is only released when they sleep.","For a cat at rest, the average heart rate usually is between 150 and 180 bpm, more than twice that of a human, which averages 70 bpm.","Compared to other felines, domestic cats have narrowly spaced canine teeth, adapted to their preferred prey of small rodents.","Cats, like dogs, are digitigrades. They walk directly on their toes, with the bones of their feet making up the lower part of the visible leg.","The domestic cat (Felis catus) is a small, typically furry, carnivorous mammal.","A cat's kidneys are so efficient, it can survive on a diet consisting only of meat, with no additional water, and can even rehydrate by drinking seawater.","Most breeds of cat have a noted fondness for settling in high places, or perching. In the wild, a higher place may serve as a concealed site from which to hunt.","The ability of a cat to reflexively twist its body and balance itself during a fall is known as the \"cat righting reflex\".","Cats have 38 chromosomes in each zygote cell.","Cucumbers look enough like a snake to cause a cat's instinctive fear of snakes to kick in, causing it to panic and flee.","The cat's tongue has backwards-facing spines about 500 μm long, which are called papillae. These contain keratin which makes them rigid so the papillae act like a hairbrush.","Among domestic cats, males are more likely to fight than females.","The softest part of a cat is most definitely its cheek area.","Hank the Cat was a Maine Coon feline that was put up as a joke candidate in the 2012 United States Senate election in Virginia, a feat which gained international coverage after Hank reportedly came third behind the two major candidates, including Vice Presidential candidate Tim Kaine.","When a cat yawns, it's mouth opens so wide that you can count every tooth.","It has been estimated that a cat yawns on the average of 109,500 times in his life.","A cat can die from essential oils","Cats can move their ears 180 degrees.","Hearing is the strongest of cat's senses: They can hear sounds as high as 64 kHz — compared with humans, who can hear only as high as 20 kHz.","Cats have free-floating clavicle bones that attach their shoulders to their forelimbs, which allows them to squeeze through very small spaces.","Lil' Bunny Sue Roux is a cat who was born with no front legs, and walks upright like a kangaroo. https://www.instagram.com/lilbunnysueroux","Blackie became the richest cat in history when he inherited 15 million British Pounds.","Colonel Meow, a Himalayan-Persian mix who became famous on social media websites for his extremely long fur and scowling face, holds the Guinness world record for longest hair on a cat (nine inches).","Towser \"The Mouser\" of Glenturret Distillery in Crieff, Scotland, holds the Guinness World Record for the most mice caught (28,899).","A cat can’t climb head first down a tree because every claw on a cat’s paw points the same way. To get down from a tree, a cat must back down.","The cat who holds the record for the longest non-fatal fall is Andy. He fell from the 16th floor of an apartment building (about 200 ft/.06 km) and survived.","Cats have about 130,000 hairs per square inch (20,155 hairs per square centimeter).","A cat’s eyesight is both better and worse than humans. It is better because cats can see in much dimmer light and they have a wider peripheral view. It’s worse because they don’t see color as well as humans do. Scientists believe grass appears red to cats.","If they have ample water, cats can tolerate temperatures up to 133 °F.","Some Siamese cats appear cross-eyed because the nerves from the left side of the brain go to mostly the right eye and the nerves from the right side of the brain go mostly to the left eye. This causes some double vision, which the cat tries to correct by “crossing” its eyes.","The lightest cat on record is a blue point Himalayan called Tinker Toy, who weighed 1 pound, 6 ounces (616 g). Tinker Toy was 2.75 inches (7 cm) tall and 7.5 inches (19 cm) long.","Many Egyptians worshipped the goddess Bast, who had a woman’s body and a cat’s head.","A cat’s nose pad is ridged with a unique pattern, just like the fingerprint of a human.","The first cartoon cat was Felix the Cat in 1919. In 1940, Tom and Jerry starred in the first theatrical cartoon “Puss Gets the Boot.” In 1981 Andrew Lloyd Weber created the musical Cats, based on T.S. Eliot’s Old Possum’s Book of Practical Cats.","Approximately 1/3 of cat owners think their pets are able to read their minds.","The claws on the cat’s back paws aren’t as sharp as the claws on the front paws because the claws in the back don’t retract and, consequently, become worn.","Like humans, cats tend to favor one paw over another.","The most traveled cat is Hamlet, who escaped from his carrier while on a flight. He hid for seven weeks behind a panel on the airplane. By the time he was discovered, he had traveled nearly 373,000 miles (600,000 km).","Approximately 40,000 people are bitten by cats in the U.S. annually.","The little tufts of hair in a cat’s ear that help keep out dirt direct sounds into the ear, and insulate the ears are called \"ear furnishings.\"","America’s cats, including housecats that adventure outdoors and feral cats, kill between 1.3 billion and 4.0 billion birds in a year.","The special sensory organ called the Jacobson's organ allows a cat to have 14 times the sense of smell of a human. It consists of two fluid-filled sacs that connect to the cat's nasal cavity and is located on the roof of their mouth behind their teeth.","The Australian Wildlife Conservancy completed a 44km cat-proof fence in 2018 to prevent feral cats from entering the Newhaven wildlife sanctuary, who kill about a million native birds every night across Australia and have caused the extinction of 20 native species.","Black cats are bad luck in the United States, but they are good luck in the United Kingdom and Australia.","The cat skull is unusual among mammals in having very large eye sockets and a powerful and specialized jaw.","Cats have scent glands along their tail, their forehead, lips, chin, and the underside of their front paws.","The french tuxedo kitty, Félix, aka \"Astrocat\", was the first cat to go to space. She survived the trip.","A male cat is called a tom.","In 1879, Belgium unsuccessfully tried to use cats to deliver mail.","Issac Newton decided to invent the cat flap because his own cat, Spithead, kept opening the door and spoiling his light experiments.","Kittens start to dream when they’re about a week old.","Thank to an extremely efficient pair of kidneys, cats can hydrate themselves by drinking salt water.","A cats whiskers are the exact width of their body.","At night, Disneyland is overrun by cats. The theme park feeds them and takes care of them though, because they keep the rodent population in check.","Cats lack antibodies against dog blood so they can only receive it via a transfusion once. The second time would kill them.","The Indiana State Prison allows prisoners to adopt a cat and keep it in their cell. They are meant to improve the mood of the prisoners.","In Islam, cats are revered for their cleanliness. Muhammad is reported to have said that \"a love of cats is an aspect of faith\".","Oftentimes shelters won't let black cats be adopted around Halloween out of a fear that they may be sacrificed.","When cats bring dead animals back to their humans, they are \"teaching them to hunt\" as they would with a younger cat.","Scottish sailer Alexander Selkirk once survived for 4 years on a deserted island thanks to feral cats that protected him from large rats during the night.","A cat named Emmy lived aboard the RMS Empress of Ireland and she never missed a voyage. On May 28,1914, however, she refused to board. The ship left without her and then sank the following day.","Domestic cats will try not to drink from a water bowl that is next to their food. This is because in the wild, water next to their kill could be contaminated.","Koko the gorilla got a pet cat for her birthday in 1985 after she requested to have one as a birthday present.","The Egyptian Mau’s name is derived from the Middle Egyptian word mjw, which means cat. But contrary to its name, it’s unclear whether the modern Egyptian Mau actually originated in Egypt.","Cat fanciers bred and exhibited Maus in Europe until World War II, when attention toward the cat waned and it nearly went extinct.","The Egyptian Mau breed was saved from extinciton when Russian princess Natalie Trubetskaya was given a Mau that was imported from the Middle East. When she emigrated to New York City in 1956, she brought along three Mau cats. She used these kitties to establish the Fatima Egyptian Mau cattery, which produced many of the ancestors of today’s Egyptian Maus in America.","The myth that a cat has nine lives comes from their ability to jump and land from high places. The number 9 is believed by some to originate from William Shakespeare's Romeo and Juliet: \"A cat has nine lives. For three he plays, for three he strays, and for the last three he stays.\"","A Chinese cat named Baidianr (meaning \"white spot\") had a unique ability to choose World Cup winners. He predicted the winner of the soccer competition 6 years in a row, before he died on June 2, 2018, just before the event ended.","Exposure to UV light with hairless or partially-hairless cats can result in sunburn, even during cloudy or shady conditions. If your cat risks overexposure, consider applying sunscreen daily.","Cats have a layer called the tapetum lucidum just behind their retina which reflects light inside the eye, helping it capture more light. This reflected light is the glow you see when taking a photo of a cat with the flash on.","Parker's cat is a Maine Coon and weights 20 pounds.","Unlike kittens, adult cats don’t release any particular key hormones during sleep. They snooze all day just because they can. :)","Russian scientists discovered in the 1930s that Siamese kittens kept in very warm rooms didn't develop the breed's signature dark patches.","Cats have a third eyelid called a “haw”. It’s generally only visible when they’re unwell.","Cats aren’t the only animals that purr — squirrels, lemurs, elephants, and even gorillas purr too.","In the original Italian version of Cinderella, the benevolent fairy godmother figure was a cat.","A cat was mayor of Talkeetna, Alaska, for 20 years. His name is Stubbs, and he died on July 23, 2017.","Many people think that the Turkish Van is simply a color variation of the Turkish Angora, but in fact, they are distinct breeds that developed in different parts of Turkey.","The Bengal is the result of crossbreeding between domestic cats and Asian leopard cats, and its name is derived from the scientific name for the Asian leopard cat (Felis bengalensis).","Despite its traditionally wild roots, the Bengal is domestic and will gladly make itself in the indoor \"jungle\" of your home.","Cat's cannot see in total darkness, however their vision is much better than a human's in semidarkness because their retinas are much more sensitive to light.","If you grow your own catnip, here's how to prepare it for kitty's enjoyment: Cut several stalks of the plant from the base. Hang them upside down in a dark and dry room for several weeks. Then cut the catnip into small pieces, rub some on your cat's favorite toys or scratching post, and let the games begin!","Approximately 80% of orange tabbies are male.","If you need to trim a cat's nails, choose a moment when he is relaxed. Massage his paws gently before clipping. Make sure you're using a pair of clippers specifically for cat's nails, not human nail clippers. Avoid cutting the nail too close to the \"quick,\" the pink part of a cat's nail, which is full of blood vessels and nerves.","The gene that codes orange fur is on the X chromosome, so female cats must inherit two orange genes to end up with orange fur, while male cats only need one.","Cats like being brushed by their owners, because it reminds them of their mother.","The collective noun for kittens is a \"kindle\".","The Manx hails from the Isle of Man in the Irish Sea, and many Manx are tailless, a condition believed to have been caused by a dominant genetic mutation that spread among the island's cat population.","Loyal, playful, and alert, Manx are considered to be more \"doglike\" than other cats.","Leonardo da Vinci created a series of drawings titled \"Study of Cat Movements and Positions\", which consists of more than twenty drawings of cats and lions lying a sleep, sitting, playing, and fighting.","I want to eat. Always. MEOW.","test"],
            shelter: 155,
            cat: null, 
            dialog:false,
            fat: true, 
            mailto: null, 
            tel: null,
            cats: [],
            loading:false,
            catData: null,
            shelters: [{ id: 153, name: "100 Mile House & District" }, { id: 154, name: "Abbotsford/Langley" }, { id: 155, name: "Alberni-Clayoquot" }, { id: 157, name: "Burnaby" }, { id: 158, name: "Campbell River" }, { id: 159, name: "Chilliwack" }, { id: 160, name: "Comox Valley & District" }, { id: 162, name: "Cowichan & District" }, { id: 163, name: "East Kootenay" }, { id: 174, name: "Haida Gwaii" }, { id: 164, name: "Kamloops" }, { id: 165, name: "Kelowna" }, { id: 194, name: "Maple Ridge" }, { id: 167, name: "Nanaimo & District" }, { id: 168, name: "Nelson" }, { id: 169, name: "North Cariboo District" }, { id: 170, name: "North Peace" }, { id: 171, name: "Parksville-Qualicum Beach & District" }, { id: 196, name: "Port Coquitlam" }, { id: 172, name: "Powell River & District" }, { id: 173, name: "Prince Rupert" }, { id: 175, name: "Quesnel & District" }, { id: 176, name: "Richmond" }, { id: 177, name: "Salt Spring Island" }, { id: 178, name: "Shuswap" }, { id: 179, name: "South Okanagan/Similkameen" }, { id: 180, name: "South Peace" }, { id: 191, name: "Special Rescue (Provincial)" }, { id: 181, name: "Squamish Valley" }, { id: 182, name: "Sunshine Coast" }, { id: 183, name: "Surrey" }, { id: 184, name: "Trail Regional" }, { id: 193, name: "Vancouver" }, { id: 185, name: "Vernon & District" }, { id: 186, name: "Victoria" }, { id: 187, name: "West Vancouver" }, { id: 189, name: "Williams Lake & District" }] 
        }
    },
    mounted() {
        this.getCatData();
    },
    watch: {
        shelter: function() {
            this.getCats();
            this.getCatFact();

        },
        fat: function() {
            this.selectCat();
            this.getCatFact();

        }
    },
    methods: {
        getCatData() {
            var self = this;
            jsonp('https://adopt.spca.bc.ca/get.php?get=animal&cat=cats', function(err, response) {
                self.catData = response;
            });
        },
        getCatFact() {
            this.catFact = this.catFacts[Math.floor(Math.random() * this.catFacts.length)];
        },
        getCats() {
            var self = this;

            this.cats = this.catData.filter(function(c) {
                return c.shelter_id == self.shelter && c.UnitWeight !== '';
            }).map(function(c) {
                let weight = 0;
                let totalWeight = parseFloat(c.TotalWeight.replace(/[^\d.-]/g, ''));
                if (c.UnitWeight === 'kg') {
                    weight = totalWeight.toFixed(2);
                }
                else {
                    weight = convert(totalWeight).from('lb').to('kg').toFixed(2);
                }
                c.Weight = weight;

                c.Photos = [c.PrimaryPhotoUrl];
                if (c.AdditionalPhotoUrls !== '') {
                    let photos = c.AdditionalPhotoUrls.split(',');
                    c.Photos = c.Photos.concat(photos)
                }
                return c;
            });
            self.selectCat();
          
        },
        selectCat() {
            var self = this;
            this.cat = null;
            let cats = this.cats.sort(function(a, b) {
               if (self.fat == true) {
                      return b.Weight - a.Weight;    
                 } else {
                     return a.Weight - b.Weight;
                 }
            
            });
            this.cat = cats[0];
            console.log(this.cat);
            this.loading = false;
        }
    },


}
</script>

